<template>
  <v-card>
    <v-data-table
      :headers="headers"
      :items="desserts"
      sort-by=""
      :single-expand="singleExpand"
      :expanded.sync="expanded"
      item-key="id"
      show-expand
      class="elevation-1"
      :search="search"
      :options.sync="options"
      :server-items-length="totalDesserts"
    >
      <template v-slot:item.enable="{ item }">
        <v-switch value :input-value="item.enable" @click.stop="updateType(item)"></v-switch>
      </template>

      <template v-slot:top>
        <v-toolbar flat color="white">
          <v-toolbar-title>企业列表</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <v-icon title="创建企业" class="ml-4" @click="addComoanyInfo">mdi-plus-thick</v-icon>
          <v-spacer></v-spacer>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
            @blur="initialize"
          ></v-text-field>
        </v-toolbar>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <v-icon
              class="mr-2"
              v-on="on"
              v-bind="attrs"
              @click="editCompanyInfo(item.id)"
            >mdi-pencil</v-icon>
          </template>
          <span>编辑</span>
        </v-tooltip>

        <delete-hint icon="mdi-delete" :func="() => { return deleteOne(item.id) }"></delete-hint>

        <!-- 续费列表 -->
        <!-- <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <v-icon class="mr-2" v-on="on" v-bind="attrs" @click="readRenewalInfo(item.uniqueCode)">mdi-currency-cny</v-icon>
          </template>
          <span>续费列表</span>
        </v-tooltip>-->

        <!-- 方式二 全屏-->
        <RenewalManage :cUniqueCode="item.uniqueCode" />
      </template>
      <template v-slot:expanded-item="{ headers, item }">
        <td :colspan="headers.length">
          {{ item.companyContent }}
          <br />
          {{ item.cropId }}
          <br />
          {{ item.securte }}
        </td>
      </template>
      <template v-slot:no-data>
        <!-- <v-btn color="yellow" @click="initialize">Reset</v-btn> -->
        <small>暂无数据</small>
      </template>
    </v-data-table>
  </v-card>
</template>


<script>
export default {
  data: () => ({
    expanded: [],
    singleExpand: true,
    dialog: false,
    search: "",
    // 分页信息
    options: {},
    totalDesserts: 0,
    headers: [
      {
        text: "公司名称",
        align: "start",
        sortable: false,
        value: "companyName"
      },
      { text: "客户数量", value: "customerNumber" },
      { text: "是否开启", value: "enable" },
      { text: "创建人", value: "creator" },
      { text: "创建时间", value: "createtime" },
      { text: "到期时间", value: "expirationTime" },
      { text: "操作", value: "actions", sortable: false },
      { text: "", value: "data-table-expand" }
    ],
    desserts: []
  }),

  computed: {},

  watch: {
    dialog(val) {
      val || this.close();
    },

    options: {
      handler(val) {
        this.options = val;
        this.initialize();
      },
      deep: true
    }
  },

  created() {
    // this.initialize();
  },

  methods: {
    initialize() {
      let getData = {
        pageNum: this.options.page,
        pageSize: this.options.itemsPerPage,
        name:this.search,
        sortBy: this.options.sortBy[0],
        sortDesc:this.options.sortDesc[0],
      };
      this.$http
        .post("/user/api/v1/company/selectCompanyList", getData)
        .then(res => {
          this.totalDesserts = res.data.total;
          this.desserts = res.data.records;
        });
    },

    addComoanyInfo() {
      this.$router.push({ path: "addCompanyInfo" });
    },

    editCompanyInfo(companyId) {
      this.$router.push({
        name: "EditCompanyInfo",
        query: { companyId: companyId }
      });
    },

    readRenewalInfo(companyUniqueCode) {
      this.$router.push({
        name: "RenewalManage",
        query: { companyUniqueCode: companyUniqueCode }
      });
    },

    deleteOne(id) {
      this.$http
        .post("/user/api/v1/company/deleteCompany", {
          id: id
        })
        .then(res => {
          if (res.success) {
            this.$http
              .get("/user/api/v1/company/selectCompanyList", {
                pageNum: this.options.page,
                pageSize: this.options.itemsPerPage
              })
              .then(res1 => {
                if (res1.success) {
                  this.totalDesserts = res1.data.total;
                  this.desserts = res1.data.records;

                  res.msg = "删除成功！";
                  this.$store.commit("SET_SNACK", res);
                } else {
                  this.$store.commit("SET_SNACK", res1);
                }
              });
          } else {
            this.$store.commit("SET_SNACK", res);
          }

          return true;
        })
        .catch(err => {
          this.$store.commit("SET_SNACK", err);
        });

    },

    updateType(item) {
      // 点击后立刻改变状态，页面立即响应，如果请求失败，在将值改回旧值
      let oldEnable = item.enable;
      item.enable = !item.enable;
      this.$http
        .post("/user/api/v1/company/updateCompany", {
          id: item.id,
          enable: !oldEnable
        })
        .then(res => {
          if (res.success) {
            // 无操作
          } else {
            item.enable = oldEnable;
            this.$store.commit("SET_SNACK", res);
          }
        })
        .catch(err => {
          item.enable = oldEnable;
          this.$store.commit("SET_SNACK", err);
        });
    },

  },

  components: {
    DeleteHint: () => import("./component/DeleteHint"),
    RenewalManage: () => import("./RenewalManage")
  }
};
</script>